import React, { Component } from 'react';

class AddPost extends Component {


    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this)
        this.handleInputChange = this.handleInputChange.bind(this)
        this.state = {
            
            title: "",
            post: "",
            tags: "",
            images: null,
            video_link: ""
            
        }
        // console.log(props)
    };

    handleSubmit(event){
        event.preventDefault();
        let data = this.state
        this.submitPost(data)
    }

    submitPost(data) {
        const endpoint = 'http://127.0.0.1:8000/api/v1/blog_post/'
        let Component = this
        let lookupOptions = {
            method: "POST",
            headers: {
                "Content-Type": "multipart/form-data",
            },
            body: JSON.stringify(data),
        }
        fetch(endpoint, lookupOptions)
        .then(function(response){
            return response.json()
        }).then(function(responseData){
            console.log(responseData)
        }).catch(function(error){
            console.log("error", error)
        })
    }

    handleInputChange(event) {
        event.preventDefault();
        // console.log(event.target.name, event.target.value)
        this.setState({
            [event.target.name]: event.target.value
        })
    }

    render() {
        return(
            <div>
                <form onSubmit={this.handleSubmit}>
                    <input type="text" placeholder="Title" name="title" className="input" onChange={this.handleInputChange} required="reaquired"></input>
                    <textarea type="textarea" name="post" className="textarea" onChange={this.handleInputChange} required="reaquired"></textarea>
                    <div class="file has-name is-right">
                      <label class="file-label">
                        <input class="file-input" name="images" type="file" onChange={this.handleInputChange} />
                          <span class="file-cta">
                            <span class="file-icon">
                              <i class="fas fa-upload"></i>
                            </span>
                            <span class="file-label">
                              Choose a file…
                            </span>
                          </span>
                          <span class="file-name">
                          </span>
                      </label>
                    </div>
                    <input type="text" placeholder="tags" name="tags" className="input" onChange={this.handleInputChange}></input>
                    <input type="submit" value="Submit" name="submit" className="button"/>
                </form>
            </div>
        )
    }
}

export default AddPost;